.Cart {
  width: 100vw;
  height: 100vh;
  background: url("data:image/png;base64,")
    repeat;
  padding: 15vh 2.5vw 15vh 2.5vw;
}
.Cart h1 {
  font-size: 2.2rem;
  margin: 0 0 2.2vh 0;
}
.YourCart {
  display: grid;
  grid-row-gap: 2.5vh;
  position: relative;
  overflow: visible;
}
.CartEmpty {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.CartEmpty h1 {
  margin: 1rem 0 0 0;
  font-size: 2rem;
  color: rgba(124, 124, 124, 0.479);
}
.cartEm {
  width: 32%;
}
.YourCart::after {
  content: "";
  position: absolute;
  width: 60%;
  bottom: -25px;
  left: 0;
  right: 0;
  margin: auto;
  border-top: 1px solid rgba(133, 133, 133, 0.856);
}
.CartItem {
  border: 1px solid #d1d1d1;
  width: 100%;
  margin: auto;
  border-radius: 10px;
  display: flex;
  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(33, 33, 33, 0.2);
}
.itemInfo {
  padding: 1.4% 0 0 0;
  width: 20%;
}
.itemInfo h3 {
  font-size: 0.9rem;
  margin: 0 0 0.2rem 0;
}
.itemInfo span {
  color: rgba(0, 0, 0, 0.719);
}
.cartImg {
  width: 34%;
  padding: 0.2rem;
  margin: 0 0.2% 0 0;
}
.cartImg img {
  display: block;
  max-width: 100%;
}
.itemQtyContainer {
  position: relative;
  padding: 1.2% 0 0 0;
  width: 34%;
}
.itemQtyContainer h2 {
  font-size: 0.9rem;
  text-align: center;
}
.itemQty {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65%;
}
.itemQty button {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  border: 1px hidden;
  background-color: rgb(214, 214, 214);
  transition: background-color 0.4s ease;
}
#increment:hover {
  background-color: #4dcc79;
}
#decrement:hover {
  background-color: rgb(248, 55, 55);
}
.itemQty h3 {
  font-size: 1.4rem;
  margin: 0 0.8rem 0 0.8rem;
}
.itemPrice {
  padding: 1% 1.2% 1% 1.2%;
  width: 36%;
  text-align: right;
}
.itemPrice h2 {
  font-size: 1rem;
  margin: 0 0 0.6rem 0;
}
.itemPrice span {
  font-size: 0.8rem;
  display: block;
  color: rgb(255, 47, 47);
  cursor: pointer;
  transition: all 0.5s ease;
  margin: 0 0 0.6rem 0;
}
.itemPrice span:hover {
  text-decoration: underline;
}
.itemPrice button {
  font-size: 0.8rem;
  padding: 5px;
  border: 1px hidden;
  border-radius: 6px;
  background-color: #4dcc79;
  color: white;
  transition: all 0.5s ease;
}
.itemPrice button:hover {
  box-shadow: 0 0 10px #4dcc79;
}

.CartSummary {
  margin: 3rem 7.2% 0 0;
  padding: 0.5rem;
  width: 100%;
}
.CartSumInfo p {
  display: flex;
  justify-content: space-between;
  margin: 0.4em 0 0.4em 0;
  font-size: 1.4rem;
}

.CartPayment {
  display: flex;
  justify-content: center;
  padding: 0.5rem;
}
.CartPayment button {
  border: 1px hidden;
  border-radius: 5px;
  background-color: #4dcc79;
  color: white;
  width: 50%;
  height: 4vh;
  font-size: 1.4rem;
  transition: box-shadow 0.5s ease;
}

.CartPayment button:hover {
  box-shadow: 0 0 10px #4dcc79;
}
.Payment {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: fit-content;
  height: fit-content;
  padding: 20px;
  z-index: 10;
}

@media screen and (min-width: 800px) {
  .Cart {
    padding: 15vh 10vw 15vh 10vw;
  }
  .CartItem {
    width: 80%;
  }
  .itemInfo {
    padding: 1.2% 0 0 0;
    width: 16%;
  }
  .itemInfo h3 {
    font-size: 1.5rem;
    margin: 0 0 0.2rem 0;
  }
  .cartImg {
    width: 13.5%;
    padding: 1rem;
    margin: 0 2% 0 0;
  }
  .CartEmpty h1 {
    font-size: 2.5rem;
  }
  .cartEm {
    width: 12%;
  }
  .itemQtyContainer {
    width: 40%;
  }
  .itemQtyContainer h2 {
    font-size: 1.7rem;
  }
  .itemQty button {
    width: 2.2rem;
    height: 2.2rem;
  }
  .itemQty h3 {
    font-size: 1.8rem;
  }
  .itemPrice {
    padding: 1.2% 0 0 0;
    width: 22%;
  }
  .itemPrice h2 {
    font-size: 1.8rem;
  }
  .itemPrice span {
    font-size: 1.2rem;
  }
  .itemPrice button {
    font-size: 1.5rem;
  }
  .CartSummary {
    width: 25%;
    float: right;
  }
}
